html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  scroll-behavior: smooth;
  font-size: 16px;
  line-height: 1.5;
  color: #606c71;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/

.mini-scroll-table .el-table__body-wrapper::-webkit-scrollbar,
.mini-scroll::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #f5f5f5;
}

/*定义滚动条轨道 内阴影+圆角*/

.mini-scroll-table .el-table__body-wrapper::-webkit-scrollbar-track,
.mini-scroll::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

/*定义滑块 内阴影+圆角*/

.mini-scroll-table .el-table__body-wrapper::-webkit-scrollbar-thumb,
.mini-scroll::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #aaaaaa;
}

.main-content {
  min-width: 300px;
  max-width: 1210px;
  padding: 35px;
  margin: auto;
  text-align: left;
  position: relative;
}

/* novel页所需全局样式 */

.novel-cover-txt {
  padding: 23px;
  font-size: 16px;
  line-height: 21px;
  overflow: hidden;
  max-height: 84px;
  background-color: beige;
}

.reader-catalog {
  height: calc(100vh - 35px);
  overflow: hidden;
  overflow-y: auto;
}

.novel-dialog .el-dialog__headerbtn {
  font-size: 24px;
}

.novel-dialog .el-dialog__header {
  padding: 0;
}

.novel-dialog .el-dialog__body {
  padding: 0;
}

/* music页所需全局样式 */

.album-picture img {
  border-radius: inherit;
}
